	          'Computer science is both fascinating and brutal. College teaches you theoretical computer science, but you\'re on your own when it comes to learning the relevant (and employable) technologies. In order to be successful in this field, you have to turn yourself into some sort of machine. You have to be able to hold focus for a long time, you have to make a lot of sacrifices with your time, there are all kinds of things I\'m doing now that I never thought I\'d be willing to two years ago. But as I devote most of my time towards bettering myself in my career, I also spend a lot of time looking from the outside in. It\'s so easy to get lost in this vortex of work day in and day out. It seems like all my ambitious colleagues have signed away their twenties, destined to spend every second working hard and aspiring to get as much money as possible, and then they will buy a house, car, and live comfortably.',
